<template>
  <div class="category">
    <dl class="m-category">
      <dt></dt>
      <dd v-for="item in list" :key="item.id">
        <a :href="'#city-' + item">{{ item }}</a>
      </dd>
    </dl>
    <dl
      v-for="(item, index) in cityList"
      :key="index"
      class="m-categroy-section"
      :id="'city-' + index"
    >
      <dt>{{ index }}</dt>
      <dd>
        <span v-for="city in item" :key="city.id" @click="changeCity(city)">{{ city.name }}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
import api from '@/api/index.js'

export default {
  data() {
    return {
      list: "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split(""),
      cityList: {}
    };
  },
  created() {
    // 
    api.getCityList().then(res => {
      let obj = {};
    // 
    res.data.data.forEach((item, index) => {
      if (!obj[item.firstChar.toUpperCase()]) {
        obj[item.firstChar.toUpperCase()] = [];
      }
      obj[item.firstChar.toUpperCase()].push(item);
    });
    this.cityList = this.objKeySort(obj);
    })
  },
  methods: {
    // 
    changeCity(item) {
      console.log(item)
      this.$store.dispatch('setPosition', item)
      this.$router.push({name: 'index'})
    },
    objKeySort(obj) {
      //1Objectkeys
      //2Arraysortnewkey
      var newkey = Object.keys(obj).sort();
      var newObj = {}; //
      for (var i = 0; i < newkey.length; i++) {
        //newkey
        newObj[newkey[i]] = obj[newkey[i]]; // 
        //newObj[newkey[i].toLowerCase()] = obj[newkey[i]]; 
      }
      return newObj; //
    }
  }
};
</script>

<style lang='scss'>
@import "@/assets/css/changecity/category.scss";
</style>


