import axios from 'axios'

// 
axios.defaults.baseURL = 'http://open.duyiedu.com'

axios.interceptors.request.use((config) => {
  // 
//   console.log(config)
  config.params = {
    ...config.params,
    appkey: 'chenjianhui_1558881939419'
  }
  return config
}, (error) => {
  // 
  return Promise.reject(error)
})

export default axios



