<template>
<!--  -->
  <div class="recommend-box-wrapper">
    <div class="recommend-box">
      <span></span>
      <ul class="recommend">
        <li v-for="item in list" :key="item.itemId">
          <!--  -->
          <div class="image">
            <img
              :src="item.imgUrl"
              alt=""
            />
          </div>
          <!--  -->
          <div class="information">
            <p class="name">{{ item.title }}</p>
            <p class="score">
              <el-rate
                disabled-void-color='#ccc'
                v-model="item.score"
                disabled
                text-color="#ff9900"
                score-template="{value}"
              ></el-rate>
              <span>{{item.commentNum}}</span>
            </p>
            <p class="address">{{item.areaName}}</p>
            <p class="pirce">
              
              <span>{{item.lowPrice}}</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list"]
};
</script>

<style>
</style>


